<template>
  <n-form-item
    class="code-input-widget"
    :class='commonClassName'
    :style='componentStyle'
    :label="showLabel ? label : ''"
    :prop="prop"
    :label-width="labelWidth"
    :rules="rules"
    @click.native="handleClickFormItem"
  >
    <div class="input-container" :class="{ resizable, border, disabled: state === 'disabled' }" :style="{height: inputHeight, width: inputWidth}">
      <div class="highlight-code" ref="codeEditor" v-loading.lock="loading"></div>
    </div>
    <div v-if="bottomTips !== ''" class="bottom-tips">
      {{ bottomTips }}
    </div>
  </n-form-item>
</template>

<script>
  import { CHANGE } from '@/config/eventName'
  import common from '@/template/mixins/common'
  import form from '@/template/mixins/form'
  import _ from 'lodash'
  import themeList from '../themes/themelist'

  const sqlStr =
    'ADD EXCEPT PERCENT ALL EXEC PLAN ALTER EXECUTE PRECISION AND EXISTS PRIMARY ANY EXIT PRINT AS FETCH PROC ASC FILE PROCEDURE AUTHORIZATION FILLFACTOR PUBLIC BACKUP FOR RAISERROR BEGIN FOREIGN READ BETWEEN FREETEXT READTEXT BREAK FREETEXTTABLE RECONFIGURE BROWSE FROM REFERENCES BULK FULL REPLICATION BY FUNCTION RESTORE CASCADE GOTO RESTRICT CASE GRANT RETURN CHECK GROUP REVOKE CHECKPOINT HAVING RIGHT CLOSE HOLDLOCK ROLLBACK CLUSTERED IDENTITY ROWCOUNT COALESCE IDENTITY_INSERT ROWGUIDCOL COLLATE IDENTITYCOL RULE COLUMN IF SAVE COMMIT IN SCHEMA COMPUTE INDEX SELECT CONSTRAINT INNER SESSION_USER CONTAINS INSERT SET CONTAINSTABLE INTERSECT SETUSER CONTINUE INTO SHUTDOWN CONVERT IS SOME CREATE JOIN STATISTICS CROSS KEY SYSTEM_USER CURRENT KILL TABLE CURRENT_DATE LEFT TEXTSIZE CURRENT_TIME LIKE THEN CURRENT_TIMESTAMP LINENO TO CURRENT_USER LOAD TOP CURSOR NATIONAL TRAN DATABASE NOCHECK TRANSACTION DBCC NONCLUSTERED TRIGGER DEALLOCATE NOT TRUNCATE DECLARE NULL TSEQUAL DEFAULT NULLIF UNION DELETE OF UNIQUE DENY OFF UPDATE DESC OFFSETS UPDATETEXT DISK ON USE DISTINCT OPEN USER DISTRIBUTED OPENDATASOURCE VALUES DOUBLE OPENQUERY VARYING DROP OPENROWSET VIEW DUMMY OPENXML WAITFOR DUMP OPTION WHEN ELSE OR WHERE END ORDER WHILE ERRLVL OUTER WITH ESCAPE OVER WRITETEXT'
  const sqlHints = [
    'SELECT',
    'INSERT',
    'DELETE',
    'UPDATE',
    'CREATE TABLE',
    'DROP TABLE',
    'ALTER TABLE',
    'CREATE VIEW',
    'DROP VIEW',
    'CREATE INDEX',
    'DROP INDEX',
    'CREATE PROCEDURE',
    'DROP PROCEDURE',
    'CREATE TRIGGER',
    'DROP TRIGGER',
    'CREATE SCHEMA',
    'DROP SCHEMA',
    'CREATE DOMAIN',
    'ALTER DOMAIN',
    'DROP DOMAIN',
    'GRANT',
    'DENY',
    'REVOKE',
    'COMMIT',
    'ROLLBACK',
    'SET TRANSACTION',
    'DECLARE',
    'EXPLAN',
    'OPEN',
    'FETCH',
    'CLOSE',
    'PREPARE',
    'EXECUTE',
    'DESCRIBE',
    'FORM',
    'ORDER BY',
  ]

  export default {
    mixins: [common, form],
    variables: {
      $value(n) {
        this.position = this.monacoInstance?.getPosition()
        this.$nextTick(() => {
          this.value = typeof n === 'string' ? n : [undefined, null].includes(n) ? n : ''
          this.monacoInstance?.setValue(this.value)
          this.monacoInstance?.setPosition(this.position)
        })
      }
    },
    data() {
      return {
        loading: false,
        hints: [],
        monacoInstance: null,
        provider: null,
        position: undefined
      }
    },
    computed: {
      rules() {
        const { required, label } = this
        const res = []
        if (required) {
          res.push(this.getTrueRule({
            required: true,
            message: `${label}`,
            trigger: 'change',
          }))
        }
        return [].reduce((prev, cur) => {
          return prev.concat({
            pattern: cur.reg,
            message: cur.errorTips,
          })
        }, res)
      },
    },
    methods: {
      initHints() {
        const dynamicHints = this.dynamicHints.split(',')
        switch (this.language) {
          case 'sql':
          case 'mysql':{
            const sqlArr = sqlStr.split(' ')
            this.hints = Array.from(
              new Set([...sqlHints, ...sqlArr, ...dynamicHints])
            ).sort()
            break
          }
          default: {
            this.hints = Array.from(new Set(dynamicHints)).sort()
            break
          }
        }
        this.hints = Array.from(new Set(dynamicHints)).sort()
      },
      dispose() {
        if (this.monacoInstance) {
          if (this.monacoInstance.getModel()) {
            this.monacoInstance.getModel().dispose()
          }
          this.monacoInstance.dispose()
          this.monacoInstance = null
          if (this.provider) {
            this.provider.dispose()
            this.provider = null
          }
        }
      },
      async init() {
        this.dispose()
        const target = this.$refs.codeEditor
        if (!target) {
          return
        }
        this.loading = true
        const src = [
          import('monaco-editor/esm/vs/editor/editor.api.js'),
          !['json'].includes(this.language) ? import(`monaco-editor/esm/vs/basic-languages/${this.language}/${this.language}.contribution`) : () => Promise.resolve(),
          !['vs', 'vs-dark'].includes(this.theme) ? import(`../themes/${themeList[this.theme]}`) : () => Promise.resolve()
        ]
        let monaco
        let lang
        let theme
        try {
          [monaco, lang, theme] = await Promise.all(src)
        } catch (e) {
          this.loading = false
          this.$message.error('')
        }
        if (!['vs', 'vs-dark'].includes(this.theme) && theme) {
          monaco.editor.defineTheme(this.theme, theme)
        }
        const that = this
        const createCompleters = (textUntilPosition) => {
          //
          const _textUntilPosition = textUntilPosition
            .replace(/[\*\[\]@\$\(\)]/g, '')
            .replace(/(\s+|\.)/g, ' ')
          //
          const arr = _textUntilPosition.split(' ')
          //
          const activeStr = arr[arr.length - 1]
          //
          const len = activeStr.length

          //
          const rexp = new RegExp('([^\\w]|^)' + activeStr + '\\w*', 'gim')
          const match = that.value?.match(rexp)
          const _hints = !match
            ? []
            : match.map((ele) => {
                const rexp = new RegExp(activeStr, 'gim')
                const search = ele.search(rexp)
                return ele.substr(search)
              })

          //
          const hints = Array.from(new Set([...that.hints, ..._hints]))
            .sort()
            .filter((ele) => {
              let rexp = new RegExp(ele.substr(0, len), 'gim')
              return (match && match.length === 1 && ele === activeStr) ||
                ele.length === 1
                ? false
                : activeStr.match(rexp)
            })
          //
          return hints.map((ele) => {
            return {
              label: ele,
              kind:
                that.hints.indexOf(ele) > -1
                  ? monaco.languages.CompletionItemKind.Keyword
                  : monaco.languages.CompletionItemKind.Text,
              documentation: ele,
              insertText: ele,
            }
          })
        }
        this.provider = monaco.languages.registerCompletionItemProvider(this.language, {
          // triggerCharacters: ['$'],
          provideCompletionItems(model, position) {
            const textUntilPosition = model.getValueInRange({
              startLineNumber: position.lineNumber,
              startColumn: 1,
              endLineNumber: position.lineNumber,
              endColumn: position.column,
            })
            const suggestions = createCompleters(textUntilPosition)
            return {
              suggestions,
            }
          },
        })

        // 
        this.monacoInstance = monaco.editor.create(target, {
          value: this.value || '',
          language: this.language,
          theme: this.theme,
          folding: false,
          foldingHighlight: true,
          foldingStrategy: 'indentation',
          showFoldingControls: 'always',
          formatOnPaste: false,
          disableLayerHinting: true,
          emptySelectionClipboard: false,
          selectionClipboard: false,
          automaticLayout: true,
          codeLens: false,
          scrollBeyondLastLine: false,
          colorDecorators: true,
          accessibilitySupport: 'off',
          lineNumbers: this.lineNumbers ? 'on' : 'off',
          lineNumbersMinChars: this.lineNumbersMinChars,
          enableSplitViewResizing: false,
          roundedSelection: false,
          overviewRulerBorder: false,
          minimap: {
            enabled: this.minimap,
          },
          autoIndent: 'full',
          readOnly: ['disabled', 'readonly'].includes(this.state),
          tabSize: this.tabSize,
          wordWrap: this.wordWrap
        })
        this.monacoInstance.onDidChangeModelContent(
          _.debounce(
            () => {
              if (!this.monacoInstance) {
                return
              }
              this.value = this.monacoInstance.getValue()
              this._emitEvent(CHANGE)
            },
            500,
            { leading: false, trailing: true }
          )
        )
        this.monacoInstance.onDidFocusEditorText(() => {
        })
        this.monacoInstance.onDidBlurEditorText(() => {
          if (!this.monacoInstance) {
            return
          }
          this.value = this.monacoInstance.getValue()
        })
        this.loading = false
      },
      reset() {
        this.initHints()
        this.init()
      },
      updateConfig(key) {
        switch(key) {
          case 'lineNumbersMinChars':
          case 'wordWrap':
          case 'tabSize': {
            this.monacoInstance.updateOptions({ [key]: this[key] })
            break
          }
          case 'lineNumbers': {
            this.monacoInstance.updateOptions({ lineNumbers: this[key] ? 'on' : 'off' })
            break
          }
          case 'state': {
            this.monacoInstance.updateOptions({ readOnly: ['disabled', 'readonly'].includes(this[key]) })
            break
          }
          case 'minimap': {
            this.monacoInstance.updateOptions({ minimap: { enabled: this[key] } })
            break
          }
        }
      }
    },
    created() {
      this.initHints()
    },
    mounted() {
      if (!this.valueInSlot && this.value === undefined) {
        this.value = this.defaultValue
      }
      this.$nextTick(() => {
        this.init()
        if (this.isEditMode) {
          ['lineNumbers', 'tabSize', 'state', 'dynamicHints', 'minimap', 'lineNumbersMinChars', 'wordWrap'].forEach(watchKey => {
            this.$watch(watchKey, () => {
              this.updateConfig(watchKey)
            })
          })
          ;['dynamicHints', 'theme', 'language'].forEach(watchKey => {
            this.$watch(watchKey, this.reset)
          })
        }
      })
    },
    beforeDestroy() {
      this.dispose()
    },
  }
</script>

<style lang="less" scoped>
  .code-input-widget::v-deep {
    .input-container {
      display: flex;
      flex-direction: column;
      justify-content: center;
      &.resizable {
        resize: both;
        overflow: auto;
      }
      &.border {
        border: 1px solid #eee;
      }
      &.disabled {
        .monaco-editor .monaco-editor-background {
          background: #eee;
        }
      }
      .highlight-code {
        width: 100%;
        height: 100%;
      }
    }
    .bottom-tips {
      color: #aaa;
      font-size: 12px;
      line-height: 1;
      padding-top: 4px;
    }
  }
</style>
