import {
  UPDATE_HISTORY_LENGTH
} from '@/config/eventName'
import _ from 'lodash'
import { SET_PAGE_CONFIG } from '@/config/storeKey'
import EventBus from './eventBus'
import { HIGH_LIGHT } from '@/config/eventKey'
class Record {
  records = [] // 
  index = 0 // 
  maxLength = 20 // 

  /**
   * 
   * @param {*} oldVal 
   * @param {*} val 
   * @param {*} extra store,  oldVal  val { oldVal: , val: , deep:  }
   * @param {function} callback 
   */
  update({
    elementId,
    oldVal,
    val,
    extra = { oldVal: {}, val: {}, deep: false },
    callback = (data) => {}
  }) {
    // : 8, 3, , 4, 
    this.records = this.records.slice(0, this.index).concat([{
      elementId,
      oldVal: _.cloneDeep(oldVal),
      val: _.cloneDeep(val),
      extra: _.cloneDeep(extra),
      callback
    }])
    this.index++
    // ,
    if (this.records.length - 1 >= this.maxLength) {
      this.records.shift()
      this.index--
    }
    this.updateView()
  }
  goForward(vueThis) {
    if (this.index === this.records.length) {
      return
    }
    const {
      elementId,
      val,
      extra : { val: extraVal, deep },
      callback
    } = _.cloneDeep(this.records[this.index])
    // 
    if(extraVal && Object.keys(extraVal).length) {
      vueThis._storeCommit(SET_PAGE_CONFIG, extraVal, deep)
    }
    // elementId, 
    if(elementId) {
      EventBus.$emit(HIGH_LIGHT, elementId)
    }
    callback(val, { type: 'forward', extraVal })
    this.index++
    this.updateView()
  }
  goBack(vueThis) {
    if (this.index === 0) {
      return
    }
    const {
      elementId,
      oldVal,
      extra: { oldVal: extraVal, deep },
      callback
    } = _.cloneDeep(this.records[--this.index])
    // 
    if(extraVal && Object.keys(extraVal).length) {
      vueThis._storeCommit(SET_PAGE_CONFIG, extraVal, deep)
    }
    // elementId, 
    if(elementId) {
      EventBus.$emit(HIGH_LIGHT, elementId)
    }
    callback(oldVal, { type: 'back', extraVal })
    this.updateView()
  }
  reset() {
    this.records = []
    this.index = 0
    this.updateView()
  }
  // 
  updateView() {
    const {
      index,
      records
    } = this
    const {
      length
    } = records
    const redoLength = index
    const undoLength = length - index
    const event = new CustomEvent(UPDATE_HISTORY_LENGTH, {
      detail: {
        redoLength,
        undoLength
      },
    })
    dispatchEvent(event)
  }
}

const instance = new Record()

export const update = instance.update.bind(instance)
export const goForward = instance.goForward.bind(instance)
export const goBack = instance.goBack.bind(instance)
export const reset = instance.reset.bind(instance)

/**
 * 
 * @param {*} keys , 
 * @returns
 */
export function getGlobalConfig(keys = []) {
    const config = this.getStore()
    const keyMap = {
      0: 'layoutData',
      1: 'jsData',
      2: 'functionsData',
      3: 'eventsData',
      4: 'dynamicProps'
    }
    if(keys && !(keys instanceof Array)) {
      keys = [keys]
    } else {
      keys = Object.keys(keyMap)
    }
    if(keys.length) {
      const result = {}
      keys.forEach(key => {
        const field = keyMap[key]
        result[field] = _.cloneDeep(config[field])
      })
      return result
    }
    return _.cloneDeep(config)
  }

export default instance