import _ from 'lodash'

export const appSettingConfig = {
  edit: false,
  preview: true,
  navigateShow: true,
  navigatePosition: 'left',
  headerShow: true
}

/**
 * 
 * @param {string} settings json
 */
export function formatSetting(settings = '{}') {
  const presetSetting = appSettingConfig
  let target
  try {
    target = JSON.parse(settings)

    for (let key in presetSetting) {
      // key, 
      // eslint-disable-next-line no-prototype-builtins
      if (!target.hasOwnProperty(key)) {
        target[key] = presetSetting[key]
      }
    }
  } catch (error) {
    target = presetSetting
  }
  return _.cloneDeep(target)
}