import {
  VERSION_INFO,
  VERSION_JUDGE
} from './mutation-types'
import {
  Message,
  MessageBox
} from 'element-ui'
import API from '@/api/application'
export default {
  namespaced: true,
  state: {
    // 
    [VERSION_INFO]: {}
  },
  getters: {},
  mutations: {
    [VERSION_INFO](state, data) {
      state[VERSION_INFO] = data
    }
  },
  actions: {
    /**
     * 
     * @param {context} context
     * @param {object}
     * {object} data ,store
     * {bollean} reload 
     * {function} createCallBack 
     * @returns promise
     */
    async [VERSION_JUDGE](context, { data, reload = true, createCallBack = () => {}, message = '', isCopy = false } = {}) {
      const {
        status,
        appId,
        id,
        version
      } = data || context.state[VERSION_INFO]
      // 
      if (status === 1 && !isCopy) {
        return Promise.resolve()
      }
      MessageBox.prompt(message || `${{ '2': '', '3': '' }[status]}!`, '', {
        confirmButtonText: '',
        cancelButtonText: '',
        closeOnClickModal: false,
        showCancelButton: true,
        inputPattern: /^(V)\d+(?:\.\d+){1,2}$/,
        inputErrorMessage: '',
        inputPlaceholder: '(: V1.0  V1.0.0)',
        inputValue: version,
        beforeClose: (action, instance, done) => {
          if (action === 'confirm') {
            instance.confirmButtonLoading = true;
            instance.confirmButtonText = '...';

            rootApp.$http({
              url: API.saveFromOtherDesign,
              data: {
                appId: appId,
                sourceAppDesignId: id,
                targetVersion: instance.inputValue
              }
            }).then(({
              data
            }) => {
              done();
              if(reload) {
                Message({
                  type: 'success',
                  message: ',',
                  duration: 1000
                });
                setTimeout(() => {
                  rootApp.$router.replace({
                    name: rootApp.$route.name,
                    query: {
                      id: data.appId,
                      vid: data.appDesignId
                    }
                  })
                  setTimeout(() => {
                    window.location.reload()
                  }, 0)
                }, 1000)
              }
              createCallBack()
            }).finally(() => {
              instance.confirmButtonLoading = false;
              instance.confirmButtonText = '';
            })
          } else {
            done();
          }
        }
      })
      return Promise.reject('')
    },
  },
}