import { CHANGE_STYLES, PAGE_COMMON_EVENTS, PLAY_ANIMATION } from '@/config/eventName'
import {
  TOGGLE_LAYOUT_VISIBLE,
  LINK_TO_PAGE,
  RUN_FUNCTION,
} from '@/config/storeKey'
import { RESET } from '@/config/eventName'
export const COMMON_FN_ASSIGN = 'COMMON_FN_ASSIGN'
export const COMMON_FN_SHOW = 'COMMON_FN_SHOW'
export const COMMON_FN_HIDE = 'COMMON_FN_HIDE'
export const COMMON_FN_LINK = 'COMMON_FN_LINK'
export const COMMON_FN_JS = 'COMMON_FN_JS'
export const COMMON_FN_PAGE_EMIT = 'COMMON_FN_PAGE_EMIT'
export const COMMON_FN_COMMON_ACTION = 'COMMON_FN_COMMON_ACTION'
import trigger from '@/renderer/tools/trigger'

export const isCommonFn = (key) => `${key}`.includes('COMMON_FN_')

export const COMMON_FNS = [
  {
    name: '',
    fn: RESET,
  },
  {
    name: '',
    fn: COMMON_FN_ASSIGN,
  },
  {
    name: '',
    fn: COMMON_FN_SHOW,
  },
  {
    name: '',
    fn: COMMON_FN_HIDE,
  },
]

export const actions = {
  [COMMON_FN_SHOW]({ params, elementId, args, logId }) {
    return this._storeCommit(TOGGLE_LAYOUT_VISIBLE, {
      elementId,
      isVisible: true,
      args,
      logId
    })
  },
  [COMMON_FN_HIDE]({ params, elementId, args, logId }) {
    return this._storeCommit(TOGGLE_LAYOUT_VISIBLE, {
      elementId,
      isVisible: false,
      args,
      logId
    })
  },
  [COMMON_FN_LINK]({ params, elementId, args }) {
    return this._storeCommit(LINK_TO_PAGE, {
      elementId,
      params,
      args
    })
  },
  [COMMON_FN_JS]({ params, elementId, args, logId }) {
    return this._storeCommit(RUN_FUNCTION, { params, args, logId })
  },
 [COMMON_FN_PAGE_EMIT]({ params, elementId, args, logId }) {
    const argsObj = (params.params || []).reduce((obj, item) => {
      obj[item.key] = item.value
      return obj
    }, {})
    const value = trigger.getObjectVariables(argsObj, [], this)
    const trueArgs = Object.entries(value).sort(([a], [b]) => Number(a.replace('param', '')) - Number(b.replace('param', ''))).map(([key, value]) => value)

    // 
    trigger.triggerComponents({ eventName: params.emitKey, elementId, args: trueArgs, vueThis: this, logId })

    // 
    const pageContainer = trigger.getRef(elementId)?.$parent
    if(pageContainer && pageContainer.elementType === 'page-container') {
      // vueThisthis, dispathcommitstoreData, provide
      trigger.triggerComponents({ eventName: params.emitKey, elementId: pageContainer.elementId, args: trueArgs, vueThis: pageContainer.$parent, logId })
    }
  },
  async [COMMON_FN_COMMON_ACTION]({ params, elementId, args, logId }) {
    const executeActions = this.getStore().eventsData[this.getStore().layoutData.elementId][PAGE_COMMON_EVENTS].filter(item => item.id === params.id)
    const result = await trigger.execeteFns(executeActions, args, this)
    trigger.recordLog({
      pid: logId,
      children: result
    })
  }
}

export const nameMap = {
  [COMMON_FN_ASSIGN]: '',
  [COMMON_FN_SHOW]: '',
  [COMMON_FN_HIDE]: '',
  [COMMON_FN_LINK]: '',
  [COMMON_FN_JS]: 'JS',
  [COMMON_FN_PAGE_EMIT]: '',
  [COMMON_FN_COMMON_ACTION]: ''
}

export default [
  {
    name: '',
    fn: CHANGE_STYLES,
  },
  {
    name: '',
    fn: PLAY_ANIMATION,
  },
]
