import { mutations, actions } from './config'
import _ from 'lodash'

const mixin = {
  provide() {
    const that = this
    return {
      getStore: () => this.storeData,
      // inject, this, 
      _storeDispatch(...[key, ...args]) {
        return actions[key].call(this, { commit: that._storeCommit, state: that.storeData } , ...args)
      },
      _storeCommit(...[key, ...args]) {
        return mutations[key].call(this, that.storeData , ...args)
      }
    }
  },
  props: {

  },
  data() {
    return {
      storeData: {
        // 
        dynamicProps: {},
        // 
        layoutData: {},
        // 
        eventsData: {},
        // 
        functionsData: [],
        // 
        jsData: [],
      }
    }
  },
  methods: {
    // mixin, this
    _storeDispatch(...[key, ...args]) {
      return actions[key].call(this, { commit: this._storeCommit, state: this.storeData } , ...args)
    },
    _storeCommit(...[key, ...args]) {
      mutations[key].call(this, this.storeData , ...args)
    }
  },
  created() {
  },
  beforeDestroy () {
    // TODO: 
    delete window.pageInstanceStore[this.storeData.layoutData.elementId]
  }
}

export default mixin

export const pageInject = Object.keys(mixin.provide())
