import { previewRoutePath } from '@/config/code-var'
import {
  formatQuery
} from '@/utils'
import qs from 'querystring'
/**
 * 
 * @param { vid } id
 * @param { pageId } id
 * @param { type }  0 [] 1 []
 */
export const jumpPreview = ({
  appVersionId,
  pageId,
  appId,
  type = 0,
}) => {
  const idKey = {
    0: 'vid',
    1: 'id'
  } [type]

  const params = {
    [idKey]: appVersionId,
    pageId,
    appId
  }
  const url = `/front${previewRoutePath}?${formatQuery(params)}`
  if (type === 0) {
    window.open(url)
  } else {
    return url
  }
}

/**
 * 
 * @param {string} url url
 * @param {object} params 
 */
export const addParamsToUrl = (url, params) => {
  const index = url.indexOf('?')
  let prefix = ''
  let paramsText = ''
  // 
  if (index !== -1) {
    prefix = url.slice(0, index)
    paramsText = url.slice(index + 1)
  } else {
    prefix = url
  }
  // 
  const concatParams = {
    ...qs.parse(paramsText),
    ...params
  }
  // 
  const keys = Object.keys(concatParams).filter(item => item.trim())
  let finallyParams = {}
  keys.forEach(key => {
    finallyParams[key] = concatParams[key]
  })
  const result = `${prefix}?${formatQuery(finallyParams)}`
  return result
}