import Vue from 'vue'
import './index.less'
// el-dialog 
Vue.directive('element-screen', {
  //  DOM 
  inserted: function (el) {
    // 
    const appendTarget = el.querySelector('.el-dialog__header')

    // 
    const fullScreenTarget = el.querySelector('.el-dialog')

    const btn = document.createElement('i')
    btn.className = 'iconfont icon-quanping2 set-full-screen'
    let isFullscreen = false
    const fullScreenClassName = ' is-fullscreen'
    btn.onclick = function () {
      // fullScreen(fullScreenTarget)
      isFullscreen = !isFullscreen
      if(isFullscreen) {
        fullScreenTarget.className = fullScreenTarget.className.replace(fullScreenClassName, '') + fullScreenClassName
      } else {
        fullScreenTarget.className = fullScreenTarget.className.replace(fullScreenClassName, '')
      }
    }
    appendTarget.appendChild(btn)
  }
})